.payment-method-container {
  display: flex;
  flex-direction: column;
  padding: 1em;
  background: white;
  border-radius: 4px;
  .rate-section {
    display: flex;
    align-items: center;
    padding-bottom: 30px;

    .rate-txt {
      margin-left: 20px;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      color: #089c;
      padding-left: 1em;
    }
  }

  .store-apply {
    .title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 600;
      color: #000000;
      font-size: 15px;
      padding-bottom: 8px;
      font-weight: 700;
    }
  }

  .see-all-branch {
    font-family: 'Google Sans', sans-serif;
    font-weight: 400;
    color: #089cff;
    font-size: 15px;
    padding-bottom: 8px;
    cursor: pointer;
  }

  .row-section {
    display: flex;
    justify-content: space-between;
  }

  .store-other {
    .title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 600;
      color: #000000;
      font-size: 15px;
      font-weight: 700;
      padding-bottom: 8px;
    }

    .see-all-txt {
      font-size: 15px;
      color: #089cff;
    }
  }

  .price-section {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #888;
    padding: 1em 0 1em 0;
    .price {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      .price-original {
        font-size: 12px;
        font-weight: 600;
        color: #000000;
        text-decoration: line-through;
      }

      .price-sale-off {
        font-size: 24px;
        font-weight: bold;
        color: #ff4355;
      }
    }
  }

  .custom-button {
    width: 100%;
    height: 3em;
    font-size: 15px;
    font-weight: 600;
  }
}
