.address-apply-container {
  display: flex;

  .address-distance {
    padding-right: 2em;
    padding-bottom: 8px;
    .address {
      display: -webkit-box;
      overflow: hidden;

      -webkit-line-clamp: 2; /* number of lines to show */
      -webkit-box-orient: vertical;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      color: #000;
      font-size: 15px;
    }

    .distance {
      margin-top: 4px;
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      color: #ff4355;
      font-size: 13px;
    }
  }
}
