.voucher-suggestion-container {
  display: flex;
  border-radius: 4px;
  border-top: 1px solid #ecedf0;
  align-items: center;
  padding: 10px 0px;
  .img-promotion {
    width: 135px;
    height: 90px;
    border-radius: 8px;
  }

  .promotion-right {
    padding: 1em;
    .title-promotion {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
    }

    .title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      font-size: 1rem;
    }
  }
  .title-store {
    font-family: 'Google Sans', sans-serif;
    font-weight: 700;
    font-size: 1rem;
  }
  .img-store {
    margin-right: 10px;
    width: 34px;
    height: 34px;
    border-radius: 17px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  @media screen and (max-width: 600px) {
    .img-promotion {
      width: 115px;
      height: 80px;
      border-radius: 8px;
    }
  }
}
