.voucher-detail-wrapper {
  display: flex;
  flex-direction: column;
  .voucher-detail-container {
    align-self: center;
    width: 85%;
    padding-top: 1em;
    .voucher_name {
      margin-left: 20px;
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 30px;
    }
    .merchant_name {
      font-family: 'Google Sans', sans-serif;
      font-style: normal;
      font-size: 17px;
    }
    .share_title {
      font-family: 'Google Sans', sans-serif;
      font-weight: 400;
      font-size: 15px;
    }
    .voucher_image {
      height: 430px;
      width: 700px;
    }
    .map_title {
      font-size: 24px;
      font-weight: 900;
      font-family: 'Google Sans', sans-serif;
    }
    .container-left {
      .tab_title {
        font-size: 24px;
        line-height: 29px;
        font-weight: bold;
        color: red;
        font-family: 'Google Sans', sans-serif;
        border-left: 4px solid #ff8647;
        padding-left: 15px;
        margin-top: 15px;
      }
      .tab_content {
        font-size: 15px;
        line-height: 25px;
        padding: 15px;
        margin-bottom: 20px;
        overflow: hidden;
        border-bottom: 1px solid #ecedf0;
      }
    }

    .container-right {
      .voucher-description {
        margin-bottom: 1em;
        background: white;
        padding: 1em;

        .title-voucher-description {
          display: flex;
          align-items: center;
        }
      }
      .promotion-section {
        background: white;
        padding: 1em;

        .promotion-title {
          color: #1b315f;
          font-size: 17px;
          padding-bottom: 1.5em;
        }
      }
    }
  }
  .merchant_info {
    margin-top: 40px;
    background-color: white;
    padding: 20px;
    border-radius: 4px;
  }
  .merchant_logo {
    border-radius: 40px;
    width: 60px;
    height: 60px;
    filter: drop-shadow(0px 4px 11px rgba(0, 0, 0, 0.25));
  }
  .merchant_name {
    margin-left: 12px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
  }
  .merchant_description {
    margin-top: 10px;
    font-family: 'Google Sans', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
  }
  .promotion-section {
    margin-top: 10px;
    background: white;
    padding: 1em;

    .promotion-title {
      font-size: 24px;
      font-weight: 700;
      font-family: 'Google Sans', sans-serif;
      font-size: 17px;
      padding-bottom: 1.5em;
    }
    .logo-home {
      width: 80px;
      height: 80px;
      border-radius: 50px;
      overflow: hidden;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }
  }
  .cart_icon {
    width: 30px;
    height: 30px;
    margin-right: 30px;
    path {
      fill: white;
    }
  }
  .img-logo-home {
    border-radius: 40px;
    width: 80px;
    height: 80px;
    filter: drop-shadow(0px 4px 11px rgba(0, 0, 0, 0.25));
  }
}
