.slide_show_container {
  text-align: center;

  .other_slide {
    // height: 70px;
    padding: 0 23px;
  }
  .main_slide {
    position: relative;
  }
  .other_slide img {
    border-radius: 4px;
    width: 95%;
    height: 70px;
    margin: 10px auto;
    object-fit: cover;
  }

  .nextArrowOtherSlide,
  .nextArrowOtherSlide:hover,
  .nextArrowOtherSlide:focus,
  .preArrowOtherSlide,
  .preArrowOtherSlide:hover,
  .preArrowOtherSlide:focus {
    width: 20px;
    height: 100%;
    background: white;
    border-radius: 2px;
    color: black;
    align-items: center;
    justify-content: center;
    display: flex;
  }

  .nextArrowOtherSlide img,
  .preArrowOtherSlide img {
    height: 20px;
    align-self: center;
  }

  .nextArrowOtherSlide::before,
  .preArrowOtherSlide::before {
    display: none;
  }
  .big_image_show {
    height: 27vw;
    width: 100%;
    object-fit: cover;
  }
  .wrapperSliderWithoutArror {
    margin: 0 auto;
  }

  .slideOtherItem {
    // outline: 0;
  }
  .next_arrow_slide {
    position: absolute;
    right: 60px;
    top: 70px;
    width: 40px;
    height: 40px;
    border-radius: 20px;

    background: #1f2d50;
    opacity: 0.5;
  }

  .pre_arrow_slide img,
  .next_arrow_slide img {
    object-fit: contain;
    width: 40px;
    height: 40px;
    padding: 10px;
  }
  .pre_arrow_slide {
    position: absolute;
    right: 60px;
    top: 70px;
    width: 40px;
    height: 40px;
    border-radius: 20px;

    background: #1f2d50;
    opacity: 0.5;
  }

  .next_arrow_slide::before,
  .pre_arrow_slide::before {
    display: none;
  }

  @media screen and (max-width: 480px) {
    // .slide_show .main_slide {
    //   height: 210px;
    // }

    // .slide_show .main_slide img {
    //   height: 210px;
    // }
  }
}
